$main-color: #000000;
$sub-color: #ffffff;
$shadow: 0px 0px 10px #000000;

* {
  font-family: "Lato", sans-serif;
}

body {
  margin: 0;
}

// header
header {
  position: relative;
  text-align: center;
  width: 100%;
}

.title {
  background: url("background-image.png") no-repeat;
  height: 480px;
  background-position: center;
  margin-bottom: 80px;
  position: relative;

  span {
    display: inline-block;
    color: $sub-color;
    font-size: 64px;
    font-weight: 100;
    line-height: 77px;
    letter-spacing: 0;
    text-shadow: $shadow;
    font-style: normal;
    top: 200px;
    left: 0;
    right: 0;
    margin: auto;
    position: absolute;
    z-index: 1;
  }

  &::before {
    background-color: rgba(0, 0, 0, 0.26);
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    content: " ";
    z-index: 0;
  }
}

.menu-bar {
  z-index: 1;
  position: absolute;
  top: 0;
  height: 60px;
  width: 100%;

  ul {
    font-size: 14px;
    padding: 0 200px;
    display: flex;
    list-style: none;
    align-items: center;
    margin: 19px 0 19px 0;

    li {
      align-self: center;
    }

    li:first-child {
      font-size: 18px;
      margin-right: auto;
    }

    a {
      color: $main-color;
      text-decoration: none;
      padding: 0 8px 0 8px;
    }
  }
  &::before {
    background: $sub-color;
    opacity: 0.2;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    content: " ";
    z-index: 999;
  }
}

.slide-button {
  position: absolute;
  bottom: 24px;
  display: flex;
  width: 100%;
  justify-content: center;

  &__circle {
    display: inline-block;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color: $sub-color;
    opacity: 0.8;
    margin: 0 4px;

    &--selected {
      border: 1px solid $main-color;
    }
  }
}

// news-image
.news {
  overflow: hidden;

  .news-image {
    width: 50vw;
    float: left;

    .image-container {
      float: right;
      position: relative;
      width: 500px;
      height: 120px;
      margin-right: 17px;
    
      &::before {
        background-color: rgba(0, 0, 0, 0.26);
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        content: " ";
        z-index: 0;
      }
    }

    img {
      width: 100%;
    }

    span {
      position: absolute;
      top: 35%;
      left: 41%;
      color: $sub-color;
      font-size: 32px;
      font-style: normal;
      font-weight: 300;
      text-shadow: $shadow;
    }
  }
}

// news-topics
.news-topics {
  width: 50vw;
  float: right;
  margin-bottom: 70px;

  .topics-container {
    margin: 5px 20px 0 20px;
    padding: 0;
    list-style: none;
    font-size: 16px;
    width: 500px;

    li {
      font-family: Noto Sans JP;
      font-size: 14px;
      line-height: 20px;
      padding-top: 8px;
      padding-bottom: 8px;
      border-bottom: 1px solid rgba(0, 0, 0, 0.12);
    }

    &:first-child {
      border-top: 1px solid rgba(0, 0, 0, 0.12);
    }
  }
}


// archives
.archives {
  margin: 0;

  .archives-title {
    text-align: center;
    font-size: 32px;
    margin-bottom: 50px;
  }

  .archives-contents {
    text-align: center;
    padding: 0 100px;

    .contents-container {
      position: relative;
      width: 238px;
      display: inline-block;
      margin: 0 12px;

      .image-wrap {
        margin-bottom: 8px;
        background-color: #000000;
        border-radius: 3px;
        height: 120px;

        .contents-image {
          position: relative;
          border-radius: 3px;
          opacity: 0.74;
        }
      }

      .description {
        font-size: 14px;
        font-weight: 700px;
        margin: 0 0 8px;
      }

      .date {
        text-align: right;
        font-size: 10px;
        color: rgba(0, 0, 0, 0.54);
        margin: 0 0 24px;
      }
    }
  }
}

// more
.button {
  text-align: center;

  .more-button {
    margin: 24px auto 100px;
    width: 300px;
    border: 1px solid $main-color;
    border-radius: 50px;

    p {
      margin: 14px auto;
    }
  }
}
